import React, { Component } from 'react';

import RandomRoller from 'react-random-roller';

import logo from './logo.svg';
import './App.css';

class App extends Component {

  constructor(props) {
    super(props);

    this.state = {
      fps: 20,
      duration: 2000
    };

    this.handleChange = this.handleChange.bind(this);
  }

  handleChange(e) {
    this.setState({
      [e.target.name]: parseInt(e.target.value)
    });
  }


  render() {
    return (
      <div className="App">
        <h1>React Random Roller</h1>
        <p>You can control the frames per second and duration by changing the values in the inputs below</p>
        <RandomRoller className="random-roller" fps={ this.state.fps } duration={ this.state.duration } list={ [1, 2, 3, 4, 5, 6] }/>

        <div>
          <label>FPS: </label>
          <input type="number" name="fps" min="1" max="60" onChange={ this.handleChange } value={ this.state.fps } />
        </div>
        <div>
          <label>Duration: </label>
          <input type="number" name="duration" min="500" max="10000" onChange={ this.handleChange } value={ this.state.duration } />
        </div>
      </div>
    );
  }
}

export default App;



